.kb {
  width: 100%;
  height: 100%;
}
.kb div {
  width: 10%;
  height: 27%;
  float: left;
  border: 2px solid rgba(114, 135, 134, 0.3);
  border-radius: 2px;
  font-size: 80px;
  font-family: Verdana-Bold, Verdana;
  font-weight: bold;
  color: #35495e;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.kb div:not(.input):hover {
  opacity: 0.8;
}
.kb div:not(.input):active {
  opacity: 0.5;
  background-color: #5dba9d;
}
.kb .input {
  width: 100%;
  height: 19%;
  background: #5dba9d;
  box-shadow: 0px 2px 8px 0px #cccccc;
  border-radius: 8px;
  border: 2px solid #5dba9d;
  font-size: 40px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  color: #ffffff;
  letter-spacing: 5px;
}
.set {
  position: absolute;
  left: 30px;
  display: block;
  width: 50px;
  height: 50px;
}
.set img {
  width: 100%;
  height: 100%;
  position: absolute;
}
.kb .input img:hover {
  opacity: 0.8;
}
.kb .input img:active {
  opacity: 0.5;
}
.kb .delete {
  width: 18%;
  background: #eb7179;
  border-radius: 0px 0px 12px 0px;
}
.kb .delete img {
  width: 140px;
  height: 120px;
  margin: 0;
}
.count {
  width:550px;
  max-width: 100%;
  height: 100%;
  background: rgba(182, 195, 189, 0.68);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-radius: 20px;
}
.count div {
  width: 30%;
  height: 110px;
  background: #ffffff;
  box-shadow: 0px 2px 2px 1px #989d9c;
  border-radius: 15px;
  font-size: 60px;
  font-family: Tahoma-Bold, Tahoma;
  font-weight: bold;
  color: #030303;
  line-height: 117px;
  text-align: center;
  cursor: pointer;
  letter-spacing: 10px;
}

.count div:hover {
  opacity: 0.8;
}
.count div:active {
  opacity: 0.5;
}
