.skus {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.menus {
  width: 100%;
  height: 104px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.skus .menus .input {
  width: 320px;
  max-width: 30%;
  min-width: 20%;
}

.input button {
  background-color: #5dba9d !important;
}
.skus .menus Button {
  width: 100px;
  margin-left: auto;
  margin-right: 20px;
  color: #FFF!important;
}





 .btn {
     color: #FFF!important;
  background-color: #5dba9d !important;
}

