.teach {
  width: 100%;
  height: 100vh;
  background: #f1f1f1;
  position: relative;
}
.teach .content {
  width: 67%;
  height: calc(100% - 529px);
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px 20px 0;
}

.teach .watch {
  width: calc(33% - 20px);
  height: calc(100% - 529px);
  padding: 3px 40px 0;
  background: #fff;
  position: absolute;
  top: 0;
  right: 0;
}
.watch Button {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  border: 2px solid #eb7179 !important;
  color: #eb7179 !important;
  background-color: #fff !important;
}
.camera {
  margin-top: 20px;
  width: 100%;
  height: calc(100% - 110px);
  background: #fafafa;
  display: flex;
  justify-content: center;
  align-items: center;
}
.camera img {
  height: 90%;
}
.teach .counterDiv {
  width: calc(33% - 20px);
  height: 509px;
  background: #fff;
  position: absolute;
  bottom: 0;
  right: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.teach .keyboard {
  width: 67%;
  height: 509px;
  margin-top: 9px;
  background: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
}

.teach .rightpanel {
  width: 32%;
  height: 100%;
  padding: 0 36px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

p {
  text-align: center;
}

.radios .check {
  background: #5dba9d;
  color: #ffffff;
}
.radios .uncheck {
  color: #333333;
  background: #f5f5f5;
}
.radios .uncheck:hover {
  opacity: 0.8;
}
.radios .uncheck:active {
  opacity: 0.5;
}
.radios {
  width: 520px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}
.radios div {
  width: 250px;
  height: 40px;
  border-radius: 40px;
  font-size: 20px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  line-height: 40px;
  text-align: center;
  letter-spacing: 3px;
  cursor: pointer;
}

.content .search {
  margin-top: 15px;
  width: 100%;
  height: calc(100% - 70px);
  display: flex;
  flex-direction: row;
  /* align-items: center; */
}
.content .search img {
  height: 80%;
  margin: 0 auto;
}
.content .search p {
  position: absolute;
  left: 50%;
  top: 60%;
  transform: translateX(-50%);
  width: 280px;
  height: 89px;
  font-size: 22px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #1f1e1e;
  line-height: 44px;
  letter-spacing: 2px;
}
.search .sku {
  width: calc(20% - 10px);
  height: 85%;
  max-height: 380px;
  background: #ffffff;
  box-shadow: 0px 40px 30px 30px rgba(219, 219, 219, 0.5);
  margin-right: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-family: PingFangSC-Semibold, PingFang SC;
  cursor: pointer;
  position: relative
}
.search .sku:active {
  opacity: 0.6;
}
.search .sku img {
  height: 50%;
  max-width: 80%;
}
.search .sku .name {
  font-size: 32px;
  font-weight: 600;
  color: #0b0b0b;
}
.search .sku .price {
  font-size: 30px;
  font-weight: 600;
  color: #e34550;
}
.search .sku .bottom {
  width: 100%;
  height: 50px;
  background: rgba(57, 197, 181, 0.08);
  border-radius: 0px 0px 20px 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.search .sku .bottom .code {
  width: 106px;
  height: 34px;
  background: #58c8be;
  box-shadow: 0px 2px 2px 0px #60a29b;
  border-radius: 0 22px 22px 0;
  font-size: 29px;
  font-weight: 600;
  color: #ffffff;
  line-height: 40px;
  text-align: center;
  letter-spacing: 1px;
}
.search .sku .bottom .total {
  text-align: right;
  font-size: 22px;
  font-weight: 500;
  color: #e37745;
  line-height: 18px;
}

.search .sku .bottom .total p {
  text-align: right;
  font-size: 10px;
  font-weight: 500;
  color: #aeaeae;
}

.sku_item {
  width: calc(100% - 80px);
  height: 250px;
  border: 2px solid #a0d7ca;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
}

.sku_item .code {
  width: 117px;
  height: 35px;
  background: #5dba9d;
  box-shadow: 0px 2px 8px 0px rgba(96, 162, 155, 0.65);
  font-size: 29px;
  font-weight: 600;
  color: #ffffff;
  line-height: 35px;
  letter-spacing: 1px;
  text-align: center;
  border-radius: 0 20px 20px 0;
}

.sku_item p {
  padding-left: 15px;
  font-size: 20px;
}
.sku_item img {
  position: absolute;
  height: 80%;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
}
.counterDiv Button {
  width: calc(100% - 80px) !important;
  height: 48px !important;
  display: flex;
  justify-content: center;
  align-items: center;
}
.counterDiv .clear_btn {
  background-color: #eb7179 !important;
  color: #fff !important;
}
.counterDiv .save_btn {
  background-color: #fff !important;
  color: #5dba9d !important;
  border: 2px solid #999 !important;
}
.counterDiv .match_btn {
  background-color: #5dba9d !important;
  color: #fff !important;
}
.content .tip1 {
  width: 30%;
  height: 40%;
  margin-left: 10%;
  margin-top: 10%;
  display: inline-block;
}

.tip1 img {
  height: 100%;
}
.tip1 p {
  width: 256px;
  height: 80px;
  font-size: 24px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #333333;
  position: relative;
  top: -30%;
  left: 10%;
}
.content .tip2 {
  width: 40%;
  height: 30%;
  margin-left: 10%;
  margin-top: 10%;
  display: inline-block;
}

.tip2 img {
  height: 100%;
}
.tip2 p {
  width: 75%;
  height: 80px;
  font-size: 24px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #333333;
  position: relative;
  top: -75%;
  left: 10%;
}
.picDiv {
  padding: 10px;
  width: 100%;
  height: calc(100% - 50px);
  overflow: auto;
}

.pic {
  width: 220px;
  height: 220px;
  float: left;
  margin-left: 20px;
  margin-top: 20px;
  position: relative;
  cursor: pointer;
  box-shadow: 0 0 3px 3px rgba(0, 0, 0, 0.1);
}
.pic img:nth-child(1) {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}
.pic img:nth-child(1):hover {
  opacity: 0.8;
}
.pic img:nth-child(1):active {
  opacity: 0.5;
}
.pic img:nth-child(2) {
  width: 30px;
  height: 30px;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  transform: translate3d(50%, -50%, 0);
}
.pic img:nth-child(2):active {
  opacity: 0.5;
}

.learning {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: absolute;
  z-index: 5;
}
.learningtext {
  width: 320px;
  height: 40px;
  line-height: 40px;
  position: absolute;
  left: calc(50% - 150px);
  top: 30%;
  z-index: 10;
  text-align: center;
  background: #5dba9d;
  font-size: 24px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  border-radius: 25px;
  color: #fff;
}

.emptySku{
  width: 100%;
  height: 100%;
  padding-top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;

}
.emptySku p{
  width: 80%!important;
  margin-top: 20px;
  position: static!important;
  transform: translateX(0)!important;
  text-align: left;
}


.sku .delete {
  width: 30px;
  height: 30px!important;
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  transform: translate3d(50%, -50%, 0);
}


.step2Empty{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.step2Empty p{
  top: 40%!important;
}


#watch{
  max-width: 80%;
  max-height: 80%;
}