.opt {
  width: 100%;
  height: 100vh;
  background: #f1f1f1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.leftpanel {
  width: 67%;
  height: 100%;
}

.rightpanel {
  width: 32%;
  height: 100%;
  background: #fff;
  padding: 0 36px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

p {
  text-align: center;
}

.item {
  width: 100%;
  height: 80px;
  border-bottom: 2px solid #cecfd3;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  font-size: 36px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #0b0b0b;
}

.item b {
  margin-left: auto;
  font-size: 53px;
  font-style: italic;
}
.item span {
  margin-left: 20px;
  font-size: 24px;
  font-family: PingFangSC-Light, PingFang SC;
  font-weight: 300;
  color: rgba(11, 11, 11, 0.51);
}
.rightpanel button {
  width: 100%;
  height: 50px !important;
  margin-top: 20px;
  font-size: 36px;
  font-family: PingFangSC-Semibold, PingFang SC;
  font-weight: 600;
  letter-spacing: 5px;
}
.rightpanel .btn1 {
  background: #fff !important;
  color: #2dbd9b !important;
  border: 2px solid #2dbd9b !important;
}

.rightpanel .btn2 {
  background: #2dbd9b !important;
  color: #fff !important;
}

.counter {
  width: 100%;
  height: 509px;
  background: rgba(182, 195, 189, 0.68);
  border-radius: 20px;
  display: flex;
}



.leftpanel .title {
  margin-left: 50%;
  transform: translateX(-50%);
  width: 399px;
  height: 45px;
  line-height: 42px;
  text-align: center;
  background: rgba(93, 186, 157, 0.19);
  border: 0;
  border-radius: 0px 0px 40px 40px;
  font-size: 30px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: rgba(11, 11, 11, 0.73);
}
.leftpanel .skus {
  width: 100%;
  height: calc(100% - 554px);
  overflow-y: auto;
}

.skus .check {
  margin-top: 20px;
  padding: 0 15%;
  width: 100%;
  height: 380px;
  background-image: url("../../assets/bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-color: rgba(0, 0, 0, 0);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  position: relative;
}
.check div {
  width: 60%;
  height: 100%;
  padding: 90px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}
.check b {
  font-size: 36px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  letter-spacing: 2px;
}

.check Button {
  position: absolute;
  top: 0;
  right: 0;
  width: 228px !important;
  height: 60px !important;
  background: #5dba9d;
  box-shadow: 0px 2px 8px 0px #cccccc;
  border-radius: 8px;
  border: 2px solid #5dba9d;
  text-align: center;
  font-size: 32px !important;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #ffffff;
  letter-spacing: 1px;
}
.leftpanel .keyboard {
  width: 100%;
  height: 500px;
  margin-top: 9px;
  background: #fff;
}

.leftpanel .sku {
  float: left;
  margin: 10px 0 12px 29px;
  width: 213px;
  height: 246px;
  padding-top: 10px;
  box-sizing: border-box;
  background-color: #fff;
  /* border-radius: 20px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
}
.leftpanel .sku:hover {
  opacity: 0.7;
}
.leftpanel .sku:active {
  opacity: 0.5;
}
.sku img {
  height: 100px;
}
.sku .name {
  height: 40px;
  font-size: 25px;
  font-weight: 500;
  color: #0b0b0b;
  letter-spacing: 1px;
}
.sku .price {
  height: 32px;
  font-size: 22px;
  font-weight: 600;
  color: #e34550;
}
.sku .bottom {
  width: 100%;
  height: 50px;
  background: rgba(57, 197, 181, 0.08);
  border-radius: 0px 0px 20px 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.bottom .code {
  width: 106px;
  height: 44px;
  background: #58c8be;
  box-shadow: 0px 2px 2px 0px #60a29b;
  border-radius: 0 22px 22px 0;
  font-size: 29px;
  font-weight: 600;
  color: #ffffff;
  line-height: 40px;
  text-align: center;
  letter-spacing: 1px;
}
.bottom .total {
  text-align: right;
  font-size: 22px;
  font-weight: 500;
  color: #e37745;
  line-height: 18px;
}

.total p {
  text-align: right;
  font-size: 10px;
  font-weight: 500;
  color: #aeaeae;
}
