@import "~antd/dist/antd.css";
@primary-color: #2DBD9B;
.container {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  box-sizing: border-box;
  padding: 5px 30px 0;
  background-color: #f2f6f4;
}

.hor_line {
  height: 1px;
  background: #728786;
  border-radius: 2px;
  opacity: 0.2;
}
.back {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  background: rgba(114, 135, 134, 0);
  border-radius: 24px;
  border: 1px solid rgba(114, 135, 134, 0.4);
}
.side {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-right: 1px solid #728786;
}
.side div {
  margin-top: 20px;
  width: 300px;
  max-width: 100%;
  height: 160px;
  border-radius: 20px;
  padding-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 26px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #35495e;
  line-height: 37px;
  text-shadow: 0px 10px 20px rgba(114, 135, 134, 0.1);
  cursor: pointer;
}
.check {
  background-color: #fff;
  box-shadow: 0px 10px 20px 0px rgba(114, 135, 134, 0.1);
}

.side div:hover {
  opacity: 0.8;
}

.side img {
  width: 60px;
  height: 60px;
  background: #35c3dc;
  box-shadow: 0px 10px 20px 0px rgba(114, 135, 134, 0.1);
  border-radius: 12px;
  margin-bottom: 10px;
}

.box {
  padding: 40px;
  height: 300px;
  background: #fff;
  box-shadow: 0px 10px 20px 0px rgba(114, 135, 134, 0.1);
  border-radius: 20px;
  border: 2px solid #16b590;
  font-size: 26px;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #333333;
  line-height: 34px;
}
.slim {
  height: 120px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.title {
  padding-left: 20px;
  width: 100%;
  height: 37px;
  font-size: 26px;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #333333;
  line-height: 27px;
  border-left: 5px solid #2dbd9b;
}

Button {
  border: 0 !important;
  font-size: 18px !important;
  font-family: PingFangSC-Medium, PingFang SC;
  font-weight: 500;
  color: #ffffff;
  line-height: 37px;
}



.container .content{
    width: 100%;
    height: 850px;
    max-height: 95%;
    min-height: 80%;
    padding: 30px 30px 0;
    position: relative;
    overflow: auto;
}
